
import { clsx } from 'clsx'

import { Slot } from '@radix-ui/react-slot'

import { ButtonHTMLAttributes, ReactNode } from 'react'

export interface ButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
    children: ReactNode;
    asChild?: boolean;
    className?: string;
}

export function Button({children, asChild, className, ...props}: ButtonProps){
    // Caso esteja sendo recebida a propriedade asChild
    // Se sim, o componente será um <Slot>. 
    // Se não, o componente será um <Span>
    const Component = asChild ? Slot : 'button'

    return(
        <Component 
            className={clsx(
                // Definições padrões
                'py-3 px-4 bg-cyan-500 rounded font-semibold text-black text-sm w-full transition-colors hover:bg-cyan-300 focus:ring-2 ring-white', className,
                )}
                {...props}
        >
            {children}
        </Component>
    )
}